import axios from "axios"

import {

    GET_BOLETINES_LIST_SUCCESS,
    GET_BOLETINES_LIST_FAIL,
    GET_BOLETINES_HOME_SUCCESS,
    GET_BOLETINES_HOME_FAIL,



} from "./types"


export const get_boletines = () => async dispatch => {
    const config = {
        Headers: {
            'Accept': " application/json"
        }


    };
    try {
        const res = await axios.get(`${process.env.REACT_APP_API_URL}/api/boletines/list`, config)

        if (res.status === 200) {
            dispatch({
                type: GET_BOLETINES_LIST_SUCCESS,
                payload: res.data
            });
        } else {
            dispatch({
                type: GET_BOLETINES_LIST_FAIL
            });
        }

    } catch (err) {
        dispatch({
            type: GET_BOLETINES_LIST_FAIL
        });

    }
}


