import {
    GET_BOLETINES_LIST_SUCCESS,
    GET_BOLETINES_LIST_FAIL,
    GET_BOLETINES_HOME_SUCCESS,
    GET_BOLETINES_HOME_FAIL,

} from '../actions/boletines/types'

const initialState = {
    boletines: null
}

export default function boletines(state = initialState, action) {
    const { type, payload } = action;

    switch (type) {
        case GET_BOLETINES_LIST_SUCCESS:
            return {
                ...state,
            
  
                boletines: payload.results.posts
            }
        case GET_BOLETINES_LIST_FAIL:
            return {
                ...state,
                boletineshome: null,
         
                next: null,
                previous: null,
            }


        case GET_BOLETINES_HOME_SUCCESS:
            return {
                ...state,
 
                boletineshome: payload.boletines
            }
        case GET_BOLETINES_HOME_FAIL:
            return {
                ...state,
                boletineshome: null,
         
          
            }
        
        default:
            return state
    }
}